@import "../../main.scss";

.categoryWrapper {
  position: absolute;
  overflow: hidden;
  overscroll-behavior: none;
  width: 100%;
  height: 100%;
  top: 0;
  transition: transform 0.5s ease-out;
}

.categoryHome {
  position: absolute;
  color: $darkColor;
  width: 100%;
  height: 100%;
  top: 0;
  transition: transform 0.3s ease-out;
}
